import { RendererCanvas } from '../../../lib'

export class Solution {
    public divHost: HTMLElement;
    public renderer: Renderer;
    constructor(id: string) {
        this.divHost = document.getElementById(id)
        // Center align
        this.divHost.style.display = 'flex';
        this.divHost.style.alignItems = 'center';

        const div = document.createElement('div');
        div.style.marginLeft = 'auto';
        div.style.marginRight = 'auto';
        div.style.backgroundColor = '#555';
        div.style.width = '1000px';
        div.style.height = '600px';

        this.divHost.appendChild(div);

        // TODO
        // You code goes here for one time operation

        this.renderer = new Renderer(div);

        // console.log(this.renderer);
        // !! you should execute it for rendering
        // this.start();
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}

export class Renderer extends RendererCanvas {

    p0: Vector;
    p1: Vector;
    p2: Vector;
    p3: Vector;

    N = 10;

    m: Vector = new Vector(0,0);

    down = false;
    capture = undefined;

    ptsList: Vector[] = [];

    constructor(div: any) {
        super(div);
        this.canvas.style.position = 'relative';
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';

        // TODO
        // You code goes here for one time operation
        // 

        this.p0 = new Vector(80, 120);
        this.p1 = new Vector(50, 50);
        this.p2 = new Vector(180, 50);
        this.p3 = new Vector(250, 120);
        this.ptsList = [this.p0, this.p1, this.p2, this.p3];

        const slider = getSlider('N', 1, 10);
        this.divHost.appendChild(slider.div);
        slider.slider.oninput = () => {
            this.N = +slider.slider.value
            // console.log(slider.slider.value)
            slider.text.innerText = 'N: ' + this.N;
        }
        // const log = document.getElementById('log');
        // const slider = document.getElementById('myRange');
        // slider.inpu
        // slider.oninput = () => {
        //     this.N = +slider.value;
        //     // render();
        //     log.innerHTML = 'N: ' + N;
        //     // console.log(N)
        // }

        this.start();
    }
    public mouseDown(x: number, y: number): void {
        // this.down = true;
        this.m.x = x;
        this.m.y = y;
        for (let i = 0; i < this.ptsList.length; ++i) {

            const dis = this.ptsList[i].distanceTo(this.m);

            if (dis < 10) {
                this.capture = this.ptsList[i];
                // console.log(this.ptsList[i]);
            }

        }
    }
    public mouseUp(x: number, y: number): void {
        this.capture = undefined;
        // this.down = false;
    }
    public mouseDrag(x: number, y: number): void {
        if (this.capture) {
            this.capture.x = x;
            this.capture.y = y;
        }
    }
    public render(ctx: CanvasRenderingContext2D): void {
        // TODO
        // You code goes here for the rending loop
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);


        this.p0.draw(ctx);
        this.p1.draw(ctx);
        this.p2.draw(ctx);
        this.p3.draw(ctx);


        let pts = [];

        let interval = 1.0 / this.N;
        for (let i = 0; i <= this.N; ++i) {
            let theInterval = interval * i;
            let v = DeCasteljausAlgorithm(this.p0, this.p1, this.p2, this.p3, interval * i);
            pts.push(v);
        }

        ctx.strokeStyle = '#00ffff';
        ctx.lineWidth = 3;
        ctx.beginPath();
        for (let i = 0; i < pts.length - 1; ++i) {
            ctx.moveTo(pts[i].x, pts[i].y)
            ctx.lineTo(pts[1 + i].x, pts[1 + i].y)
        }
        ctx.stroke();


        ctx.strokeStyle = '#000';
        ctx.lineWidth = 1;
        ctx.beginPath();
        ctx.moveTo(this.p0.x, this.p0.y);
        ctx.lineTo(this.p1.x, this.p1.y);
        ctx.stroke();

        ctx.beginPath();
        ctx.moveTo(this.p2.x, this.p2.y);
        ctx.lineTo(this.p3.x, this.p3.y);
        ctx.stroke();

        // !! can stop render after this frame.
        // this.isStatic = true;
    }

}


// https://codepen.io/NJStudio/pen/zYwZmEL?editors=1010
class Vector {
    x = 0;
    y = 0;
    constructor(x = 0, y = 0) {
        this.x = x;
        this.y = y;
    }
    distanceTo(other: Vector) {
        return Math.sqrt((other.x - this.x) * (other.x - this.x) + (other.y - this.y) * (other.y - this.y));
    }
    draw(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#ffffff';
        ctx.beginPath();
        ctx.arc(this.x, this.y, 5.0, 0.0, 3.14159 * 2)
        ctx.closePath();
        ctx.fill();
    }
}
const interpolation = (p0: Vector, p1: Vector, t0: number, t1: number) => {
    return new Vector(
        t1 * p0.x + t0 * p1.x,
        t1 * p0.y + t0 * p1.y
    );
};
const DeCasteljausAlgorithm = (p0: Vector, p1: Vector, p2: Vector, p3: Vector, t0: number) => {
    //Linear interpolation = lerp = (1 - t) * A + t * B
    let t1 = 1.0 - t0;
    //Layer 1 
    let QVector = interpolation(p0, p1, t0, t1);
    let RVector = interpolation(p1, p2, t0, t1);
    let SVector = interpolation(p2, p3, t0, t1);

    //Layer 2
    let PVector = interpolation(QVector, RVector, t0, t1);
    let TVector = interpolation(RVector, SVector, t0, t1);

    //Final interpolated position
    return interpolation(PVector, TVector, t0, t1);
};

// ..

const getSlider = (title: string, min: number, max: number) => {
    // <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
    const div = document.createElement('div');
    div.style.display = 'flex';

    const slider = document.createElement('input');
    slider.type = 'range';
    slider.min = `${min}`;
    slider.max = `${max}`;

    const text = document.createElement('div');
    text.innerText = title;
    text.style.color = '#aaa';
    div.appendChild(slider);
    div.appendChild(text);

    return {
        div, slider, text
    };
};